import {
  POST_LOGIN,
  POST_LOGOUT,
  LOGIN_IN_PROGRESS,
} from '../actions/loginActions';

const initialState = {
  isLoggedIn: false,
  loginInProgress: false,
  loginError: false,
};

const loginReducer = (state = initialState, action) => {
  switch (action.type) {
    case POST_LOGIN:
      if (action.success) {
        return {
          ...state,
          isLoggedIn: action.success,
          details: action.details,
        };
      }
      return {
        ...state,
        isloggedIn: action.success,
        loginError: true,
      };

    case POST_LOGOUT:
      return {
        ...state,
        isLoggedIn: false,
      };

    case LOGIN_IN_PROGRESS:
      return {
        ...state,
        loginInProgress: action.payload,
      };
    default:
      return state;
  }
};

export default loginReducer;
