import axios from 'axios';
import { push } from 'connected-react-router';

// We should never store sensitive information in query params
const loginAPI = '/home/login?userName=Admin&pwd=Admin';

export const POST_LOGIN = 'POST_LOGIN';
export const LOGIN_IN_PROGRESS = 'LOGIN_IN_PROGRESS';
export const POST_LOGOUT = 'POST_LOGOUT';

export const postLogin = payload => dispatch => {
  dispatch(loginInProgress(true));
  axios
    .post(loginAPI)
    .then(res =>
      setTimeout(() => {
        dispatch({
          type: POST_LOGIN,
          success: true,
          details: res.data,
        });
        dispatch(loginInProgress(false));
        dispatch(push('/dashboard'));
      }, 2000)
    )
    .catch(err => {
      console.log(err);
      dispatch(loginInProgress(false));
      dispatch({
        type: POST_LOGIN,
        success: false,
      });
    });
};

export const loginInProgress = payload => ({
  type: LOGIN_IN_PROGRESS,
  payload,
});

const logout = () => ({
  type: POST_LOGOUT,
});

export const postLogout = () => dispatch => {
  dispatch(logout());
  dispatch(push('/'));
};
