import {
  TOGGLE_TRANSCRIBE,
  UPDATE_INTERIM_SCRIPT,
  UPDATE_FINAL_SCRIPT,
} from '../actions/dashboardActions';

const initalState = {
  transcribeInProgress: false,
  interimScript: '',
  finalScript: '',
};

const dashboardReducer = (state = initalState, action) => {
  switch (action.type) {
    case TOGGLE_TRANSCRIBE:
      return { ...state, transcribeInProgress: action.transcribeInProgress };
    case UPDATE_INTERIM_SCRIPT:
      return { ...state, interimScript: action.inputScript };
    case UPDATE_FINAL_SCRIPT:
      return { ...state, finalScript: action.inputScript };
    default:
      return state;
  }
};

export default dashboardReducer;
