import React from 'react';

import 'antd/dist/antd.css';

import { Layout, Menu, Icon, Typography } from 'antd';
import { Link } from 'react-router-dom';

const { Title } = Typography;
const { Header, Content, Footer, Sider } = Layout;
const { SubMenu } = Menu;

export default class AppLayout extends React.PureComponent {
  state = {
    collapsed: true,
  };

  onCollapse = collapsed => {
    console.log(collapsed);
    this.setState({ collapsed });
  };

  navHomeLinksTo = isLoggedIn => {
    return isLoggedIn ? '/dashboard' : '/login';
  };

  render() {
    const { isLoggedIn } = this.props;
    return (
      <Layout style={{ minHeight: '100vh' }}>
        <Sider
          collapsible
          collapsed={this.state.collapsed}
          onCollapse={this.onCollapse}
        >
          <div className="logo" />
          <Menu theme="dark" defaultSelectedKeys={['1']} mode="inline">
            <Menu.Item key="1">
              <Link to={this.navHomeLinksTo(isLoggedIn)}>
                <Icon type="home" />
                <span>Home</span>
              </Link>
            </Menu.Item>
            {isLoggedIn && (
              <SubMenu key="sub1" title={<Icon type="user" />}>
                <Menu.Item key="3">
                  <Link to="/accounts">My Accounts</Link>
                </Menu.Item>
                <Menu.Item key="5" onClick={this.props.postLogout}>
                  Logout
                </Menu.Item>
              </SubMenu>
            )}
          </Menu>
        </Sider>
        <Layout>
          <Header
            style={{ background: '#fff', padding: 0, textAlign: 'center' }}
          >
            <Title level={2}>Can I Buy It Today?</Title>
          </Header>
          <Content style={{ margin: '0 16px' }}>{this.props.children}</Content>

          <Footer style={{ textAlign: 'center' }}>Done by Team 8</Footer>
        </Layout>
      </Layout>
    );
  }
}
