import React from 'react';

import '../../styles/fab.scss';

export const MB = p => (
  <button type="button" className="rtf--mb" {...p}>
    {p.children}
  </button>
);

class Fab extends React.Component {
  static defaultProps = {
    position: { bottom: 0, right: 0 },
    event: 'hover',
  };

  state = { open: false };

  enter = () => this.props.event === 'hover' && this.open();

  leave = () => this.props.event === 'hover' && this.close();

  open = () => this.setState({ open: true });

  close = () => this.setState({ open: false });

  toggle = () => {
    if (this.props.event === 'click') {
      this.state.open ? this.close() : this.open();
    }
  };

  render() {
    const { position, icon, mainButtonStyles, onClick } = this.props;
    return (
      <ul
        onMouseEnter={this.enter}
        onMouseLeave={this.leave}
        className={`rtf ${this.state.open ? 'open' : 'closed'}`}
        data-testid="fab"
        style={position}
      >
        <li className="rtf--mb__c">
          <MB
            onClick={onClick}
            style={mainButtonStyles}
            data-testid="main-button"
            role="button"
            aria-label="Floating menu"
            tabIndex="0"
          >
            {icon}
          </MB>
        </li>
      </ul>
    );
  }
}

export { Fab };
