export const TOGGLE_TRANSCRIBE = 'TOGGLE_TRANSCRIBE';
export const UPDATE_INTERIM_SCRIPT = 'UPDATE_INTERIM_SCRIPT';
export const UPDATE_FINAL_SCRIPT = 'UPDATE_FINAL_SCRIPT';

export const toggleTranscribe = transcribeInProgress => ({
  type: TOGGLE_TRANSCRIBE,
  transcribeInProgress,
});

export const updateInterimScript = inputScript => ({
  type: UPDATE_INTERIM_SCRIPT,
  inputScript,
});

export const updateFinalScript = inputScript => ({
  type: UPDATE_FINAL_SCRIPT,
  inputScript,
});
