import { connect } from 'react-redux';
import {
  toggleTranscribe,
  updateInterimScript,
  updateFinalScript,
} from '../../../actions/dashboardActions';
import index from './index';

const mapStateToProps = state => ({
  transcribeInProgress: state.dashboard.transcribeInProgress,
  interimScript: state.dashboard.interimScript,
  finalScript: state.dashboard.finalScript,
});

const mapDispatchToProps = dispatch => ({
  toggleTranscribe: transcribeInProgress =>
    dispatch(toggleTranscribe(transcribeInProgress)),
  updateInterimScript: interimScript =>
    dispatch(updateInterimScript(interimScript)),
  updateFinalScript: finalScript => dispatch(updateFinalScript(finalScript)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(index);
