import React from 'react';
import { Progress, Card, Icon, Typography } from 'antd';
import styled from 'styled-components';

import { Fab } from '../../commons/Fab';

const { Title, Text } = Typography;

const Container = styled.div`
  /* width: 260px; */
  /* min-height: 50px; */
  /* border-radius: 5px; */
  /* box-shadow: 3px 3px 5px 1px rgba(207, 207, 207, 0.5); */
  background-color: #ffffff;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 0 2px;
  margin-bottom: 10px;
`;

const StyledCard = styled(Card)`
  width: calc(100% / 3);
  min-height: 50px;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
`;

const ProgressBarCard = styled(Card)`
  min-height: 50px;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  margin-bottom: 10px;
`;

const months = [
  'January',
  'Feburary',
  'March',
  'April',
  'May',
  'June',
  'July',
  'August',
  'September',
  'October',
  'November',
  'December',
];

export default class Dashboard extends React.PureComponent {
  onSpeechRecognitionClick = () => {
    let recognition;
    const {
      transcribeInProgress,
      updateInterimScript,
      updateFinalScript,
    } = this.props;
    if (!transcribeInProgress) {
      window.SpeechRecognition =
        window.webkitSpeechRecognition || window.SpeechRecognition;
      recognition = new window.SpeechRecognition();
      recognition.interimResults = true;
      recognition.maxAlternatives = 10;
      recognition.continuous = false;
      recognition.start();
      this.props.toggleTranscribe(true);
      recognition.onresult = event => {
        let interimTranscript = '';
        let finalTranscript = '';
        for (let i = event.resultIndex; i < event.results.length; i += 1) {
          const { transcript } = event.results[i][0];
          if (event.results[i].isFinal) {
            finalTranscript += transcript;
            updateFinalScript(finalTranscript);
            recognition.stop();
            this.props.toggleTranscribe(false);
          } else {
            interimTranscript += transcript;
            updateInterimScript(interimTranscript);
          }
        }
      };
    } else {
      recognition.stop();
      this.props.toggleTranscribe(transcribeInProgress);
    }
  };

  render() {
    return (
      <div>
        <Title level={3}>
          {`${months[new Date().getMonth()]} ${new Date().getFullYear()}`}
        </Title>

        <ProgressBarCard>
          <Text>You are spending within limits</Text>
          <Progress percent={50} />
        </ProgressBarCard>

        <Container>
          <StyledCard loading={false}>
            <Text>Expenses</Text>
            <br />
            <Text>$1337</Text>
          </StyledCard>
          <StyledCard loading={false}>
            <Text>Income</Text>
            <br />
            <Text>$7331</Text>
          </StyledCard>
          <StyledCard loading={false}>
            <Text>Balance</Text>
            <br />
            <Text>$1500</Text>
          </StyledCard>
        </Container>
        <Title style={{ textAlign: 'center' }} level={2}>
          {this.props.interimScript}
        </Title>
        {!this.props.transcribeInProgress && (
          <Fab
            icon={<Icon type="smile" />}
            event="click"
            onClick={this.onSpeechRecognitionClick}
          />
        )}
      </div>
    );
  }
}
